import React, {
  useState,
  useRef,
  useEffect,
  memo,
  useMemo,
  useCallback,
} from "react";
import FormErrorMessage from "components/UI/common/ErrorMessage";
import CustomController from "components/UI/common/Controller";
import { useFieldArray, useFormContext, useWatch } from "react-hook-form";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import RedDeleteIcon from "assets/icons/redDeleteIcon.svg";
import InputBox from "components/UI/common/InputBox/InputBox";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import { numberInput } from "helpers";
import IconButton from "@mui/material/IconButton";

import styles from "./index.module.scss";

function capitalize(str) {
  if (str) return str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();
  else return "";
}

const TableBody = (props) => {
  const {
    watch,
    trigger,
    control,
    setValue,
    formState: { errors },
    register,
    getValues,
    clearErrors,
  } = useFormContext();
  const {
    saveDraft,
    activeTab,
    i,
    roomTypeIndex,
    handleDeletedArray,
    sharingData,
    setSharingData,
    showPreview,
  } = props;

  const occupancyDropdownData = [
    { value: "FULL_ROOM", label: "Full Room" },
    { value: "SHARING", label: "Sharing" },
  ];

  const [state, setState] = useState({
    sharingDropdownData: [],
    occupancyDropdownData: [...occupancyDropdownData],
  });

  useEffect(() => {
    hideOccupancySelectedOptions();
    hideSharingSelectedOptions();
  }, []);

  useEffect(() => {
    if (sharingData) {
      setState((prev) => ({
        ...prev,
        sharingDropdownData: [...sharingData],
      }));
    }
    hideSharingSelectedOptions();
  }, [sharingData]);

  const fieldsPath = useMemo(
    () => `fieldArray.${roomTypeIndex}.data.${i}.data`,
    [i, roomTypeIndex]
  );

  const { fields, insert, remove, append, update } = useFieldArray({
    control,
    name: fieldsPath,
  });

  const handleSharingAddProperty = (value) => {
    if (value?.fieldArray) {
      setSharingData((prev) => [
        ...prev,
        ...value.fieldArray.map((o) => ({ value: o.name, label: o.name })),
      ]);
    }
  };

  const sharingFullRoom = useMemo(() => [{ label: "NA", value: "NA" }], []);

  const contractType = useMemo(
    () => [
      { value: "DAILY", label: "Daily" },
      { value: "MONTHLY", label: "Monthly" },
    ],
    []
  );

  const paymentDropdownData = [
    { value: "Stay and Pay", label: "Stay and Pay" },
    { value: "Pay and Stay", label: "Pay and Stay" },
  ];

  const handleOccupancyAdd = async (j, field, fieldPath) => {
    const isValid = await trigger(`${fieldsPath}.${j}.occupancy`);
    if (isValid) {
      append(
        {
          propertyId: field.propertyId,
          roomType: field.roomType,
          roomTypeId: field.roomTypeId,
          stayType: field.stayType,
          occupancy: "",
          sharing: "",
          baseAdults: "",
          baseChild: "",
          extraAdult: "",
          extraChild: "",
          contractType: field.stayType === "SHORT_STAY" ? "DAILY" : "MONTHLY",
          baseRate: "",
          minThresholdRate: "",
          maxThresholdRate: "",
          extraAdultCost: "",
          extraChildCost: "",
          rentPaymentModel: "",
          securityDepositMonths: field.stayType === "SHORT_STAY" ? "NA" : "",
          tokenAmountPercentage: "",
        },
        {
          shouldFocus: false,
        }
      );
    }
    hideSharingSelectedOptions();
    hideOccupancySelectedOptions();
  };

  const watchFields = watch(fieldsPath);

  const handleOccupancyDelete = (j, field) => {
    const occupancy = watch(`${fieldsPath}.${j}.occupancy`);
    remove(j);
    if (field._id) {
      handleDeletedArray(field._id);
    }
    hideOccupancySelectedOptions();
    hideSharingSelectedOptions();
  };

  const handleSharingAdd = async (j, field, fieldPath) => {
    const isValid = await trigger(`${fieldsPath}.${j}.sharing`);
    const isOccupancyFilledValid = await trigger(
      `${fieldsPath}.${j}.occupancy`
    );
    const watchFields = watch(fieldsPath);
    const watchField = watch(fieldPath);
    const fullRoomPosition = watchFields.findIndex(
      (o) => o.occupancy === "FULL_ROOM"
    );
    const emptyOccupancyPosition = watchFields.findIndex(
      (o) => o.occupancy === ""
    );
    if (
      isOccupancyFilledValid &&
      isValid &&
      fields.length < sharingData.length
    ) {
      let index = j + 1;
      if (fullRoomPosition === 0) {
        index = watchFields.length - 1;
      }
      if (fullRoomPosition !== -1 && fullRoomPosition !== 0) {
        index = watchFields.length - 2;
      }
      if (fullRoomPosition === -1 && emptyOccupancyPosition === -1) {
        index = watchFields.length - 1;
      }
      insert(index + 1, {
        propertyId: field.propertyId,
        roomType: field.roomType,
        roomTypeId: field.roomTypeId,
        stayType: field.stayType,
        occupancy: null,
        sharing: "",
        baseAdults: "",
        baseChild: "",
        extraAdult: "",
        extraChild: "",
        contractType: field.stayType === "SHORT_STAY" ? "DAILY" : "MONTHLY",
        baseRate: "",
        minThresholdRate: "",
        maxThresholdRate: "",
        extraAdultCost: "",
        extraChildCost: "",
        rentPaymentModel: "",
        securityDepositMonths: field.stayType === "SHORT_STAY" ? "NA" : "",
        tokenAmountPercentage: "",
        hideOccupancy: true,
      });
      hideSharingSelectedOptions();
    }
  };

  const handleSharingDelete = (i, j, field) => {
    remove(j);
    // eslint-disable-next-line no-undef
    // handleDeletedArray(F)
    hideSharingSelectedOptions();
    if (field._id) {
      handleDeletedArray(field._id);
    }
  };

  const renderErrorField = (error, name) => {
    return (
      <div className={styles.errorWrap}>
        <FormErrorMessage
          error={error?.[name]}
          messages={{
            required: `Required`,
            maxLength: "Should contain max 2 digits ",
          }}
        />
      </div>
    );
  };

  const hideOccupancySelectedOptions = useCallback(() => {
    const watchFields = watch(fieldsPath);
    const occupancyArr = watchFields.map((field) => field.occupancy);
    setState((prev) => ({
      ...prev,
      occupancyDropdownData: occupancyDropdownData.filter(
        (o) => !occupancyArr.includes(o.value)
      ),
    }));
  }, [fieldsPath, occupancyDropdownData]);

  const hideSharingSelectedOptions = useCallback(() => {
    const watchFields = watch(fieldsPath);
    const sharingArr = watchFields.map(
      (field) => field.sharing && Number(field.sharing)
    );
    setState((prev) => ({
      ...prev,
      sharingDropdownData: sharingData.filter(
        (o) => !sharingArr.includes(Number(o.value))
      ),
    }));
  }, [fieldsPath, sharingData]);

  const occupancyRows = useMemo(() => {
    return watchFields.filter(
      (field, n) =>
        field.occupancy === "SHARING" ||
        field.occupancy === "FULL_ROOM" ||
        field.occupancy === ""
    );
  }, [watchFields]);

  const emptyOccupancyRowPosition = useMemo(
    () => watchFields.findIndex((field, n) => field.occupancy === ""),
    [watchFields]
  );

  const fullRoomPosition = useMemo(
    () => watchFields.findIndex((field, n) => field.occupancy === "FULL_ROOM"),
    [watchFields]
  );

  const sharingRow = useMemo(
    () =>
      watchFields.filter(
        (field, n) => field.occupancy === "SHARING" || field.occupancy === null
      ),
    [watchFields]
  );
  const showAddIcon = useCallback(
    (j) => {
      const isSharingAdd =
        watchFields.length === 1 ||
        (watchFields.length === 2 &&
          emptyOccupancyRowPosition === -1 &&
          fullRoomPosition === -1 &&
          j === 0) ||
        (watchFields.length >= 3 &&
          emptyOccupancyRowPosition === -1 &&
          fullRoomPosition === -1 &&
          j === watchFields.length - 1) ||
        (sharingRow.length === 2 &&
          fullRoomPosition === -1 &&
          j === 0 &&
          emptyOccupancyRowPosition !== -1) ||
        (emptyOccupancyRowPosition !== 0 &&
          emptyOccupancyRowPosition !== -1 &&
          watchFields.length > 3 &&
          emptyOccupancyRowPosition - 1 === j) ||
        (fullRoomPosition !== 0 &&
          fullRoomPosition !== -1 &&
          watchFields.length > 3 &&
          fullRoomPosition - 1 === j) ||
        (fullRoomPosition === 0 && watchFields.length === 2 && j === 1) ||
        (fullRoomPosition === 0 && watchFields.length === 3 && j === 1) ||
        (fullRoomPosition === 0 &&
          watchFields.length > 3 &&
          j === watchFields.length - 1) ||
        (fullRoomPosition !== 0 &&
          fullRoomPosition !== -1 &&
          watchFields.length === 3 &&
          j === 0);
      const checkEmptyOccupancy =
        (emptyOccupancyRowPosition !== 0 &&
          emptyOccupancyRowPosition !== -1 &&
          j === watchFields.length - 1) ||
        (fullRoomPosition !== 0 &&
          fullRoomPosition !== -1 &&
          watchFields.length === 2 &&
          j === 0) ||
        (emptyOccupancyRowPosition !== 0 &&
          emptyOccupancyRowPosition !== -1 &&
          watchFields.length === 2 &&
          j === 0);
      const checkFullRoom =
        (fullRoomPosition !== 0 &&
          fullRoomPosition !== -1 &&
          j === watchFields.length - 1) ||
        (fullRoomPosition === 0 && fullRoomPosition === j);

      return isSharingAdd || checkEmptyOccupancy || checkFullRoom;
    },
    [
      fullRoomPosition,
      emptyOccupancyRowPosition,
      watchFields.length,
      sharingRow.length,
    ]
  );

  const showOccupancyAddIcon = (j) => {
    return (
      occupancyRows.length === 1 ||
      occupancyRows.length != 2 ||
      sharingRow.length === watchFields.length
    );
  };

  const getUpdatedSharingOption = (value = null) => {
    if (!value || value == 0) return state.sharingDropdownData;
    if (!state?.sharingDropdownData?.length)
      return [{ value: String(value), label: String(value) }];
    if (
      !state?.sharingDropdownData?.some((opt) => opt?.value === String(value))
    ) {
      return [
        ...state.sharingDropdownData,
        { value: String(value), label: String(value) },
      ];
    }
    return state.sharingDropdownData;
  };

  return fields.map((field, j, array) => {
    const fieldPath = `${fieldsPath}.${j}`;
    const error = errors?.fieldArray?.[roomTypeIndex]?.data?.[i]?.data?.[j];
    let showSharingAdd = true;
    const watchField = watch(fieldPath);
    const fieldRef =
      control._fields.fieldArray?.[roomTypeIndex]?.data?.[i]?.data[j];
    console.log(getValues(`${fieldPath}.occupancy`), field.stayType);
    return (
      <tr
        key={field.id}
        className={
          j === watchFields.length - 1 && i === 0 ? styles.borderBottom : ""
        }
      >
        <td
          className={styles.fixed}
          style={
            j === 0
              ? field.stayType == "LONG_STAY"
                ? { borderTop: "1px solid #f6f6f6" }
                : { border: "none" }
              : { border: "none" }
          }
        >
          {j === 0
            ? field.stayType === "LONG_STAY"
              ? "Long Stay"
              : "Short Stay"
            : ""}
        </td>
        <td
          style={
            getValues(`${fieldPath}.occupancy`) !== "FULL_ROOM"
              ? { borderTop: "none", borderBottom: "none" }
              : { borderTop: "1px solid #f6f6f6" }
          }
        >
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {capitalize(getValues(`${fieldPath}.occupancy`))}
              </div>
            </div>
          ) : (
            <>
              {watchField.occupancy !== null && (
                <div className={styles.selectcontroller}>
                  <CustomController
                    name={`${fieldPath}.occupancy`}
                    control={control}
                    error={error?.occupancy}
                    rules={{ required: saveDraft ? false : true }}
                    messages={{ required: "Required" }}
                    render={({
                      field: { onChange, ref, value },
                      ...fields
                    }) => {
                      return (
                        <div className="d-flex align-items-center">
                          <NormalMultiSelect
                            {...fields}
                            selectRef={ref}
                            className={styles.occupancySelect}
                            options={state.occupancyDropdownData}
                            name={`${fieldPath}.occupancy`}
                            placeholder="Enter type"
                            hideSelectedOptions={true}
                            handleChange={(e, { value } = {}) => {
                              onChange(value);
                              hideSharingSelectedOptions();
                              hideOccupancySelectedOptions();

                              if (j === 0 && value === "FULL_ROOM") {
                                watchFields.forEach((o, n) => {
                                  if (o.occupancy === null) {
                                    setValue(
                                      `${fieldsPath}.${n}.occupancy`,
                                      "SHARING"
                                    );
                                  }
                                });
                              }

                              const newValue =
                                value === "SHARING" || value === null
                                  ? "NA"
                                  : "";
                              getValues(`${fieldPath}.occupancy`);

                              setValue(`${fieldPath}.baseChild`, newValue, {
                                shouldValidate: !!error?.baseChild,
                              });
                              setValue(`${fieldPath}.extraAdult`, newValue, {
                                shouldValidate: !!error?.extraAdult,
                              });
                              setValue(`${fieldPath}.extraChild`, newValue, {
                                shouldValidate: !!error?.extraChild,
                              });
                              setValue(
                                `${fieldPath}.extraAdultCost`,
                                newValue,
                                {
                                  shouldValidate: !!error?.extraAdultCost,
                                }
                              );
                              setValue(
                                `${fieldPath}.extraChildCost`,
                                newValue,
                                {
                                  shouldValidate: !!error?.extraAdultCost,
                                }
                              );

                              const sharingValue =
                                value === "FULL_ROOM" ? "NA" : "";
                              setValue(`${fieldPath}.sharing`, sharingValue, {
                                shouldValidate: !!error?.sharing,
                              });
                            }}
                            value={value}
                            menuPosition="fixed"
                          />

                          {showOccupancyAddIcon() ? (
                            <IconButton
                              className={`${styles.iconbtn}`}
                              onClick={() =>
                                handleOccupancyAdd(j, field, fieldPath)
                              }
                            >
                              <AddCircleOutlineIcon
                                className={styles.circleAdd}
                              />
                            </IconButton>
                          ) : (
                            <IconButton
                              className={styles.iconbtn}
                              onClick={() => handleOccupancyDelete(j, field)}
                            >
                              <img
                                className={styles.deleteicon}
                                src={RedDeleteIcon}
                                alt="delete icon"
                              />
                            </IconButton>
                          )}
                        </div>
                      );
                    }}
                  />
                </div>
              )}
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.sharing`)}
              </div>
            </div>
          ) : (
            <>
              <div className={styles.selectcontroller}>
                <CustomController
                  name={`${fieldPath}.sharing`}
                  control={control}
                  error={error?.sharing}
                  rules={{ required: saveDraft ? false : true }}
                  messages={{ required: "Required" }}
                  render={({ field: { onChange, ref, value }, ...fields }) => {
                    const sharingOptions =
                      watch(`${fieldPath}.occupancy`) === "FULL_ROOM"
                        ? sharingFullRoom
                        : getUpdatedSharingOption(
                            watch(`${fieldPath}.sharing`)
                          );
                    return (
                      <div className="d-flex align-items-center">
                        <NormalMultiSelect
                          {...fields}
                          selectRef={ref}
                          inputType={"number"}
                          inputPlaceholder={"Enter value"}
                          showValue={value}
                          className={styles.occupancySelect}
                          hideSelectedOptions={false}
                          options={sharingOptions}
                          addProperty
                          addPropertyLabel="Add new"
                          isSearchable={false}
                          name={`${fieldPath}.sharing`}
                          placeholder="Enter no"
                          // newOptions={sharingDropdownData}
                          handleAdd={handleSharingAddProperty}
                          handleChange={(e, { value } = {}) => {
                            onChange(value);
                            trigger(`${fieldPath}.occupancy`);
                            hideSharingSelectedOptions();
                            if (value) {
                              setValue(`${fieldPath}.baseAdults`, value, {
                                shouldValidate: true,
                              });

                              const newValue =
                                watchField.occupancy === null ||
                                watchField.occupancy === "SHARING" ||
                                (value !== "NA" && value !== "")
                                  ? "NA"
                                  : "";
                              setValue(`${fieldPath}.baseChild`, newValue, {
                                shouldValidate: !!error?.baseChild,
                              });
                              setValue(`${fieldPath}.extraAdult`, newValue, {
                                shouldValidate: !!error?.extraAdult,
                              });
                              setValue(`${fieldPath}.extraChild`, newValue, {
                                shouldValidate: !!error?.extraChild,
                              });
                              setValue(
                                `${fieldPath}.extraAdultCost`,
                                newValue,
                                {
                                  shouldValidate: !!error?.extraAdultCost,
                                }
                              );
                              setValue(
                                `${fieldPath}.extraChildCost`,
                                newValue,
                                {
                                  shouldValidate: !!error?.extraAdultCost,
                                }
                              );
                            }
                          }}
                          value={value}
                          menuPosition="fixed"
                          disabled={
                            watch(`${fieldPath}.occupancy`) === "FULL_ROOM"
                          }
                        />
                        {true ? (
                          showAddIcon(j) ? (
                            <IconButton
                              className={`${styles.iconbtn} ${styles.iconbtn2}`}
                              onClick={() =>
                                handleSharingAdd(j, field, fieldPath)
                              }
                              disabled={
                                watch(`${fieldPath}.occupancy`) === "FULL_ROOM"
                              }
                            >
                              <AddCircleOutlineIcon
                                className={styles.circleAdd}
                              />
                            </IconButton>
                          ) : (
                            <IconButton
                              className={styles.iconbtn}
                              onClick={() => handleSharingDelete(i, j, field)}
                            >
                              <img
                                className={styles.deleteicon}
                                src={RedDeleteIcon}
                                alt="delete icon"
                              />
                            </IconButton>
                          )
                        ) : null}
                      </div>
                    );
                  }}
                />
              </div>{" "}
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.baseAdults`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                isDefaultValue={false}
                onKeyDown={numberInput}
                name={`${fieldPath}.baseAdults`}
                isWhiteDisableBg
                placeholder="Enter no."
                inputClass={styles.input}
                grayedBox={false}
                register={register(`${fieldPath}.baseAdults`, {
                  required:
                    saveDraft ||
                    (watchField.sharing !== "NA" && watchField.sharing)
                      ? false
                      : true,
                  maxLength: 2,
                  validate: {
                    iszero: (value) => {
                      return saveDraft || value !== "0";
                    },
                    isFourthSharing: (value) => {
                      if (saveDraft) return true;
                      return watchField.sharing &&
                        watchField.occupancy !== "FULL_ROOM"
                        ? Number(value) <= Number(watchField.sharing)
                        : true;
                    },
                  },
                })}
                value={watchField.baseAdults}
                disabled={
                  watchField.sharing !== "" && watchField.sharing !== "NA"
                }
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.baseAdults}
                  messages={{
                    required: `Required`,
                    iszero: "Invalid",
                    isFourthSharing: "Invalid",
                    maxLength: "Should contain max 2 digits ",
                  }}
                />
              </div>{" "}
            </>
          )}
        </td>
        <td
          className={watchField.baseChild === "NA" ? styles.notapplicable : ""}
        >
          {watchField.baseChild === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.baseChild`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                isDefaultValue={false}
                onKeyDown={numberInput}
                name={`${fieldPath}.baseChild`}
                placeholder="Enter no."
                inputClass={styles.input}
                register={register(`${fieldPath}.baseChild`, {
                  required: saveDraft ? false : true,
                  maxLength: 2,
                })}
                value={watchField.baseChild}
                disabled={
                  watchField.occupancy === "SHARING" ||
                  (watchField.sharing !== "NA" && watchField.sharing !== "") ||
                  watchField.occupancy === null
                }
              />
              {renderErrorField(error, "baseChild")}
            </>
          )}
        </td>
        <td
          className={watchField.extraAdult === "NA" ? styles.notapplicable : ""}
        >
          {watchField.extraAdult === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.extraAdult`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                isDefaultValue={false}
                name={`${fieldPath}.extraAdult`}
                placeholder="Enter no."
                onKeyDown={numberInput}
                inputClass={styles.input}
                register={register(`${fieldPath}.extraAdult`, {
                  required: saveDraft ? false : true,
                  maxLength: 2,
                  validate: {
                    checkzero: (value) => {
                      if (value === "0") {
                        setValue(`${fieldPath}.extraAdultCost`, "0");
                      }
                      return true;
                    },
                  },
                })}
                value={watchField.extraAdult}
                disabled={
                  watchField.occupancy === "SHARING" ||
                  (watchField.sharing !== "NA" && watchField.sharing !== "") ||
                  watchField.occupancy === null
                }
              />
              {renderErrorField(error, "extraAdult")}
            </>
          )}
        </td>
        <td
          className={watchField.extraChild === "NA" ? styles.notapplicable : ""}
        >
          {watchField.extraChild === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.extraChild`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                isDefaultValue={false}
                onKeyDown={numberInput}
                name={`${fieldPath}.extraChild`}
                placeholder="Enter no."
                inputClass={styles.input}
                register={register(`${fieldPath}.extraChild`, {
                  required: saveDraft ? false : true,
                  maxLength: 2,
                  validate: {
                    checkzero: (value) => {
                      if (value === "0") {
                        setValue(`${fieldPath}.extraChildCost`, "0");
                      }
                      return true;
                    },
                  },
                })}
                value={watchField.extraChild}
                disabled={
                  watchField.occupancy === "SHARING" ||
                  (watchField.sharing !== "NA" && watchField.sharing !== "") ||
                  watchField.occupancy === null
                }
              />
              {renderErrorField(error, "extraChild")}
            </>
          )}
        </td>
        <td>
          <div className={styles.selectcontroller}>
            <div className={`d-flex align-items-center p-3 fw-400`}>
              {capitalize(getValues(`${fieldPath}.contractType`))}
            </div>
            {/* <CustomController
                name={`${fieldPath}.contractType`}
                control={control}
                error={error?.contractType}
                rules={{
                  required: saveDraft ? false : true,
                  validate: {
                    validateType: (value) => {
                      if (saveDraft) return true
                      return (
                        (watchField.stayType === "SHORT_STAY" &&
                          value === "DAILY") ||
                        (watchField.stayType === "LONG_STAY" &&
                          value === "MONTHLY")
                      );
                    },
                  },
                }}
                messages={{
                  required: "Required",
                  validateType: "Invalid",
                }}
                render={({ field: { onChange, ref, value }, ...fields }) => {
                  return (
                    <div className="d-flex">
                      <NormalMultiSelect
                        {...fields}
                        className={styles.renderPaymentSelect}
                        options={contractType}
                        name={`${fieldPath}.contractType`}
                        placeholder="Select type"
                        handleChange={(e, { value } = {}) => {
                          onChange(value);
                        }}
                        menuPosition="fixed"
                        value={value}
                      />
                    </div>
                  );
                }}
              /> */}
          </div>
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.baseRate`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                isDefaultValue={false}
                onKeyDown={numberInput}
                name={`${fieldPath}.baseRate`}
                placeholder="Enter amount"
                inputClass={styles.input}
                register={register(`${fieldPath}.baseRate`, {
                  required: saveDraft ? false : true,
                  validate: {
                    isZero: (value) => {
                      if (watchField.minThresholdRate) {
                        trigger(`${fieldPath}.minThresholdRate`);
                      }
                      if (watchField.maxThresholdRate) {
                        trigger(`${fieldPath}.maxThresholdRate`);
                      }
                      return value !== "0";
                    },
                  },
                })}
                value={watchField.baseRate}
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.baseRate}
                  messages={{
                    required: `Required`,
                    isZero: "Invalid",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.minThresholdRate`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                name={`${fieldPath}.minThresholdRate`}
                isDefaultValue={false}
                placeholder="Enter amount"
                inputClass={styles.input}
                register={register(`${fieldPath}.minThresholdRate`, {
                  required: saveDraft ? false : true,
                  validate: {
                    isMoreBaseRate: (value) => {
                      if (saveDraft) return true;
                      if (
                        watchField.baseRate > 0 &&
                        Number(value) >= Number(watchField.baseRate)
                      ) {
                        return false;
                      }
                      return true;
                    },
                    isBaseRate: (value) => {
                      return saveDraft || watchField.baseRate !== "";
                    },
                  },
                })}
                type="number"
                onKeyDown={numberInput}
                value={watchField.minThresholdRate}
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.minThresholdRate}
                  messages={{
                    required: `Required`,
                    isMoreBaseRate: "Enter less than base rate",
                    isBaseRate: "Enter base rate",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.maxThresholdRate`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                name={`${fieldPath}.maxThresholdRate`}
                isDefaultValue={false}
                placeholder="Enter amount"
                inputClass={styles.input}
                register={register(`${fieldPath}.maxThresholdRate`, {
                  required: saveDraft ? false : true,
                  validate: {
                    isLessBaseRate: (value) => {
                      if (
                        watchField.baseRate > 0 &&
                        Number(value) <= Number(watchField.baseRate)
                      ) {
                        return false;
                      }
                      return true;
                    },
                    isBaseRate: (value) => {
                      return saveDraft || watchField.baseRate !== "";
                    },
                  },
                })}
                type="number"
                onKeyDown={numberInput}
                value={watchField.maxThresholdRate}
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.maxThresholdRate}
                  messages={{
                    required: `Required`,
                    isLessBaseRate: "Enter more than base rate",
                    isBaseRate: "Enter base rate",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td
          className={
            watchField.extraAdultCost === "NA" ? styles.notapplicable : ""
          }
        >
          {watchField.extraAdultCost === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.extraAdultCost`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                name={`${fieldPath}.extraAdultCost`}
                isDefaultValue={false}
                onKeyDown={numberInput}
                placeholder="Enter amount"
                inputClass={styles.input}
                register={register(`${fieldPath}.extraAdultCost`, {
                  required:
                    saveDraft || watchField.extraAdultCost === "NA"
                      ? false
                      : true,
                  validate: {
                    isBaseRate: (value) => {
                      return saveDraft || watchField.baseRate ? true : false;
                    },
                    isLessThanBase: (value) => {
                      if (saveDraft) return true;
                      if (value && value !== "NA") {
                        return Number(value) <= Number(watchField.baseRate);
                      }

                      return true;
                    },
                    isExtraAdult: (value) => {
                      return true;
                    },
                  },
                })}
                value={watchField.extraAdultCost}
                disabled={
                  watchField.occupancy === "SHARING" ||
                  (watchField.sharing !== "NA" && watchField.sharing !== "") ||
                  watchField.occupancy === null
                }
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.extraAdultCost}
                  messages={{
                    required: `Required`,
                    isLessThanBase: "Enter less than base rate",
                    isBaseRate: "Enter base rate",
                    isExtraAdult: "Invalid",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td
          className={
            watchField.extraChildCost === "NA" ? styles.notapplicable : ""
          }
        >
          {watchField.extraChildCost === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.extraChildCost`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                name={`${fieldPath}.extraChildCost`}
                placeholder="Enter amount"
                isDefaultValue={false}
                inputClass={styles.input}
                register={register(`${fieldPath}.extraChildCost`, {
                  required:
                    saveDraft || watchField.extraChildCost === "NA"
                      ? false
                      : true,
                  validate: {
                    isBaseRate: (value) => {
                      return (
                        saveDraft ||
                        watchField.baseRate !== "" ||
                        value === "NA"
                      );
                    },
                    isLessThanBase: (value) => {
                      if (saveDraft) return true;
                      if (value && value !== "NA") {
                        return Number(value) <= Number(watchField.baseRate);
                      }
                      return true;
                    },
                    isExtraChild: (value) => {
                      return true;
                    },
                  },
                })}
                type="number"
                onKeyDown={numberInput}
                value={watchField.extraChildCost}
                disabled={
                  watchField.occupancy === "SHARING" ||
                  (watchField.sharing !== "NA" && watchField.sharing !== "") ||
                  watchField.occupancy === null
                }
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.extraChildCost}
                  messages={{
                    required: `Required`,
                    isLessThanBase: "Enter less than base rate",
                    isBaseRate: "Enter base rate",
                    isExtraChild: "Invalid",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.rentPaymentModel`)}
              </div>
            </div>
          ) : (
            <div className={styles.selectcontroller}>
              <CustomController
                name={`${fieldPath}.rentPaymentModel`}
                control={control}
                error={error?.rentPaymentModel}
                rules={{ required: saveDraft ? false : true }}
                messages={{ required: "Required" }}
                render={({ field: { onChange, ref }, ...fields }) => {
                  return (
                    <div className="d-flex">
                      <NormalMultiSelect
                        {...fields}
                        className={styles.renderPaymentSelect}
                        options={paymentDropdownData}
                        isSearchable
                        name={`${fieldPath}.rentPaymentModel`}
                        placeholder="Select type"
                        handleChange={async (e, { value } = {}) => {
                          onChange(value);
                          if (
                            error?.tokenAmountPercentage &&
                            value === "Pay and Stay"
                          ) {
                            clearErrors(`${fieldPath}.tokenAmountPercentage`);
                          }
                        }}
                        menuPosition="fixed"
                        value={watchField.rentPaymentModel}
                      />
                    </div>
                  );
                }}
              />
            </div>
          )}
        </td>
        <td
          className={
            watchField.securityDepositMonths === "NA"
              ? styles.notapplicable
              : ""
          }
        >
          {watchField.securityDepositMonths === "NA" ? (
            <div>NA</div>
          ) : showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.securityDepositMonths`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                type="number"
                onKeyDown={numberInput}
                isDefaultValue={false}
                name={`${fieldPath}.securityDepositMonths`}
                placeholder="Enter no."
                inputClass={styles.input}
                register={register(`${fieldPath}.securityDepositMonths`, {
                  required:
                    saveDraft || watchField.stayType === "SHORT_STAY"
                      ? false
                      : true,
                  validate: {
                    iszeroLongStay: (value) => {
                      if (
                        watchField.stayType === "LONG_STAY" &&
                        value === "0"
                      ) {
                        return false;
                      }
                      return true;
                    },
                  },
                })}
                value={watchField.securityDepositMonths}
                disabled={watchField.stayType === "SHORT_STAY"}
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.securityDepositMonths}
                  messages={{
                    required: `Required`,
                    iszeroLongStay: "Invalid",
                  }}
                />
              </div>
            </>
          )}
        </td>
        <td>
          {showPreview ? (
            <div className={styles.selectcontroller}>
              <div className={`d-flex align-items-center p-3 fw-400`}>
                {getValues(`${fieldPath}.tokenAmountPercentage`)}
              </div>
            </div>
          ) : (
            <>
              <InputBox
                name={`${fieldPath}.tokenAmountPercentage`}
                isDefaultValue={false}
                placeholder="Enter no."
                onKeyDown={numberInput}
                inputClass={styles.input}
                register={register(`${fieldPath}.tokenAmountPercentage`, {
                  required:
                    saveDraft ||
                    (watchField.rentPaymentModel !== "Pay and Stay" &&
                      watchField.rentPaymentModel !== "")
                      ? false
                      : true,
                  validate: {
                    isZero: (value) => {
                      if (watchField.rentPaymentModel !== "") {
                        return value !== "0";
                      }
                      return true;
                    },
                  },
                })}
                type="number"
                value={watchField.tokenAmountPercentage}
              />
              <div className={styles.errorWrap}>
                <FormErrorMessage
                  error={error?.tokenAmountPercentage}
                  messages={{
                    required: `Required`,
                    isZero: "Invalid",
                  }}
                />
              </div>
            </>
          )}
        </td>
      </tr>
    );
  });
};

export default memo(TableBody);
